
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import api from '../api/api'
import env from '../config/env'
export default {
    data() {
        return {
            id: -1,
            product: {},
            productItem: [],
            name:"",
            age:0,
            email:'',
            qualification: '',
            qualificationList: [
                {key: '0', value: ''}, 
                {key: '1', value: ''}, 
                {key: '2', value: ''}],
            sexList:  [{
                key: '0',
                value: ''
            }, {
                key: '1',
                value: ''
            }],
            sex:'',
            areaCodeList:[
                {key: '01', value: ''},
                {key: '02', value: ''},
                {key: '03', value: ''},
                {key: '04', value: ''},
                {key: '05', value: ''},
                {key: '06', value: ''},
                {key: '07', value: ''},
                {key: '08', value: ''},
                {key: '09', value: ''},
                {key: '10', value: ''},
                {key: '11', value: ''}],
            area_code: '',
            button:[{
                style: 'primary',
                text: '',
                onButtonClick: (name) => {
                    // 
                    let vm = this
                    // vip
                    if((!this.isVip && this.validInput()) || this.isVip) {
                        this.$vux.confirm.show({
                            title: env.alertInfo,
                            content: '',
                            hideOnBlur: true,
                            confirmText: '',
                            cancelText: '',
                            maskTransition: '',
                            dialogTransition: '',
                            onConfirm () {
                                vm.order()
                            }
                        })
                    }
                }
            }],
            showLoading: false,
            loadingType: 'lines',
        }
    },
    computed: {
        isVip() {
            return this.$store.state.user.memberShip.membership_level == '1'
        }
    },
    mounted() {
        this.id = this.$route.params.id
        this.initData()
    },
    methods: {
        async initData() {
            let vm = this
            // 
            vm.showLoading = true
            // id
            api.get('getProductById/' + vm.id).then((res) => {
                if(res.status===1){
                    // 
                    vm.product = res.data
                    vm.title = vm.product.type === '0' ? '' : ''
                    vm.productItem.push({label: "", value: vm.product.brand})
                    vm.productItem.push({label: "", 
                        value: ""+vm.product.original_price.toString()})
                    vm.productItem.push({label: "", 
                        value: ""+vm.product.member_price.toString()})
                    if(vm.title === ''){
                        vm.productItem.push({label: "", value: vm.product.quantity.toString()})
                    }
                    vm.productItem.push({label: "", value: vm.product.description})
                }else{
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
                // 
                vm.showLoading = false
            }).catch(error => {
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        },
        getImgUrl(img_url) {
            return img_url && env.serverUrl + img_url
        },
        // 
        order() {
            let vm = this
            vm.showLoading = true
            api.post('addWechatOrder', 
            { product_id: this.id, 
            order_price: this.product.member_price, 
            member_id: localStorage.member_id,
            name: this.name,
            age: this.age,
            email: this.email,
            qualification: this.qualification,
            sex: this.sex,
            area_code: this.area_code,
            membership_level: this.$store.state.user.memberShip.membership_level
            }).then(res => {
                vm.showLoading = false
                if (res.status == '1') {
                    // 
                    if (!vm.isVip) {
                        // store
                        vm.$store.dispatch('initUser', {member_id: localStorage.member_id}) 
                    }
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '',
                        dialogTransition:'',
                        maskTransition:'',
                        onHide () {
                            // 
                            vm.$router.replace({path: '/orderDetail/'+ res.data.id});
                        }
                    })
                } else {
                    // 
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
            }).catch(error => {
                vm.showLoading = false
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        },
        // 
        validInput(){
            if (!this.name) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return false
            }
            if (!this.age) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return false
            }
            if (!this.email) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return false
            }
            if (!this.sex) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return false
            }
            if (!this.qualification) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return false
            }
            if (!this.area_code) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return false
            }
            return true
        },
        back() {
            this.$router.replace({path: '/shop'});
        }
    },
    components: {
        Spinner,
        XHeader,
        FormPreview,
        XInput,
        Group,
        Radio,
        Selector
    }
}



